import React from 'react';
import styled from 'styled-components';

const ContentWrap = styled.div`
  width: 100%;

  aside {
    display: flex;
    padding: 20px;
  }
`;

const Block = styled.div`
  display: flex;
  margin: 4pc 12pc;

  .img-wrap {
    width: 30pc;
    padding: 34px;
  }
  .img-wrap img {
    width: 100%;
  }

`;

const TextWrap = styled.div`
  width: 60%;
  
  h3 {
    font-size: 28px;
    color: #FF5178;
  }
`;

const Content = ({ blockList }) => {
  return (
    <ContentWrap>
      {blockList.map(block => {
        return (
          <Block>
            <TextWrap>
              <h3>{block.title}</h3>
              <p>
                {block.text}
              </p>
            </TextWrap>
            {block.image ? (
              <aside>
                <div className="img-wrap">
                  <img src={`${process.env.PUBLIC_URL}/images/${block.image}`}  alt="sis-econ" />
                </div>
              </aside>
            ) : (
              ''
            )}
          </Block>
        );
      })}
    </ContentWrap>
  );
}

export default Content;